@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800|Open+Sans:300,300i,400,400i,600,700,800|Droid+Serif:400,400i);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-blue: #0d3de5;
  --color-black: #343a40;
  --color-gry: #c1c1c1;
  --color-white: #ffff;
}

body
{
  font-family: 'Montserrat', sans-serif !important;
  overflow-x: hidden;
  margin-bottom: 0 auto;
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
}

p {
  /* margin: 0 !important; */
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
}

.all-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a {
  text-decoration: none !important;
}

.btn-c {
  font-size: 1rem;
  line-height: 1.5;
  border: 2px solid #343a40;
  border: 2px solid var(--color-black);
  background-color: #343a40;
  background-color: var(--color-black);
  padding: 10px 30px;
  border-radius: 25px;
  color: #ffff;
  color: var(--color-white);
  transition: 0.5s;
  cursor: pointer;
  position: relative;
  top: 10px;
}

.btn-c:hover{
  background-color: #ffff;
  background-color: var(--color-white);
  border: 2px solid #343a40;
  border: 2px solid var(--color-black);
  transition: 0.5s;
  color: #343a40;
  color: var(--color-black);
}

.btn-c.outlined {
  font-size: 1rem;
  line-height: 1.5;
  border: 2px solid #343a40;
  border: 2px solid var(--color-black);
  background-color: #ffff;
  background-color: var(--color-white);
  padding: 10px 30px;
  border-radius: 25px;
  color: #343a40;
  color: var(--color-black);
  transition: 0.5s;
  cursor: pointer;
  position: relative;
  top: 10px;
}

.btn-c.outlined:hover{
  background-color: #343a40;
  background-color: var(--color-black);
  border: 2px solid #343a40;
  border: 2px solid var(--color-black);
  transition: 0.5s;
  color: #ffff;
  color: var(--color-white);
}

.cursor-pointer { cursor: pointer;}

.special-text {
  font-weight: 800;
  color: #ffb3d7;
}

.lead {
  font-size: 1.25rem;
  font-weight: 300;
}

.info {
  font-weight: 500;
  font-family: "droid serif", serif;
  font-style: italic;
  color: gray;
}

.scalable {
  transform: scale(1);
  transition: 0.3s;
  cursor: pointer;
}

.scalable:hover {
  transform: scale(1.05);
}
.top-bar {
    background-color: #fbfcfe;
    z-index: 100;
    padding-top: 2px;
    padding-bottom: 2px;
}

.top-bar .list-inline-item {
    padding: 0;
    margin-right: 0;
}

.top-bar .list-inline-item a {
    font-size: 12px;
    display: block;
    color: #999;
    margin: 0!important;
    margin-right: 0;
}

.top-bar .list-inline-item {
    position: relative;
    padding: 0 7px!important;
}

.top-bar .list-inline-item:first-child {
    padding-left: 0;
}

.top-bar .list-inline-item:not(:last-child):before {
    content: "";
    position: absolute;
    right: -3px;
    width: 2px;
    height: 30%;
    top: 35%;
    background: rgba(0, 0, 0, .1);
}

.header
{
    background-color: var(--color-white);
    padding: 1% 0;
}

.header .logo 
{
    padding-top: 2px;
}

.header .logo img
{
    width: 30px;
    height: 30px;
    position: relative;
    top: -2px;
}

.header .logo i
{
    color: orange;
    padding-right: 10px;
    font-size: 25px;
}

.header .logo a
{
    font-size: 25px;
    font-weight: bold;
    color: var(--color-black);;
}

.header .nav
{
    float: right;
    padding-top: 2%;
}

.header .nav > ul
{
    list-style: none;
    display: flex;
}

.header .nav li 
{
    font-size: 13px;
}

li>a.bold { font-weight: bold; }

li.head { font-weight: bold; padding: 10px 0 10px 0; }

.header .nav a
{
    font-size: 1rem;
    color: rgba(0, 0, 0, .7);
    padding: 1.3rem .9rem;
    text-transform: uppercase;
}

.header .nav li a:hover
{
    color: var(--color-orange);
    transition: 0.5s;
    opacity: 0.5s;
}

.header .parent-nav li {
    position: relative;
}

.header .child-nav {
    list-style: none;
    position: absolute;
    top: 40px;
    left: 0;
    z-index: 1;
    width: 510px;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 0 10px #ccc;
    display: none;
}
 
.header .child-nav.open {
    display: block;
}

/* menu button  */

.mobile-menu-toggle {
    display: inline-block;
    display: none;
    cursor: pointer;
    color: var(--color-black);
    position: absolute;
    top: 8px;
    right: 20px;
    z-index: 2;
}

.mobile-menu-toggle:hover { color: var(--color-black); }

.bar1, .bar2, .bar3 {
    width: 30px;
    height: 3px;
    border-radius: 5px;
    background-color: var(--color-black);
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    transform: rotate(-45deg) translate(-6px, 6px);
    background-color: var(--color-white);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    transform: rotate(45deg) translate(-7px, -7px);
    background-color: var(--color-white);
}

/* menu desktop and mobile switching on responsive  */

.desktop-menu {
    display: block;
}

/* sidemenu */

.sidenav 
{
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 1;
    top: 0;
    left: -100%;
    background-color: var(--color-black);
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 100px;
}

.sidenav.open {
    left: 0;
}

.sidenav.close {
    left: -100%;
}

.sidenav a 
{
    padding: 10px 30px;
    text-decoration: none;
    font-size: 25px;
    color: var(--color-white);
    display: block;
    text-align: left;
}

.sidenav a:hover { 
    color: var(--color-black); 
}

.sidenav .logo i{ 
    font-size: 40px;
}

.mobile-menu li::marker {
    content: "";
}

/* menu desktop and mobile switching on responsive  */
@media only screen and (max-width: 768px) {
    .mobile-menu-toggle {
        display: block;
        top: 38px;
    }

    .header .nav {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .header{
        padding: 2% 0;
    }

    .mobile-menu-toggle
    {
        top: 65px;
    }

    .header .child-nav {
        width: 90%;
        top: 60px;
    }

    .header .child-nav li {
        font-size: 12px;
    }

    .sidenav a:hover { 
        color: var(--color-white); 
    }
}
footer {
    padding: 2rem  0 0 0;
    box-shadow: 0 -3px 5px -5px #80808085;
    font-family: montserrat, sans-serif;
    font-size: .85rem;
}

footer .list-inline-item:not(:last-child):before {
    content: "";
    position: absolute;
    right: -10px;
    width: 3px;
    height: 40%;
    top: 33%;
    background: rgba(0, 0, 0, .1);
}

footer .list-inline-item {
    position: relative;
    padding: 0 10px!important;
}

footer #back-to-the-top {
    cursor: pointer;
    font-size: 1rem;
}

footer a {
    color: inherit;
}

footer a:hover {
    text-decoration: none;
    color: inherit;
}

.list-inline-item:not(:last-child) {
    margin-right: .5rem;
}
/* slider section  */

.slider {
    padding: 8rem 6rem 12rem 8rem;
}

.slider h1 {
    font-size: 4rem;
    font-weight: 800;
}

.slider h4 {
    line-height: 25px;
    font-size: 25px;
    margin-bottom: 0.25rem;
    font-family: "droid serif", serif;
    font-style: italic;
}

/* after-slider section  */

.after-slider {
    padding: 3.5rem 0rem;
    background-color: #2c2f32f7;
}

.after-slider h2
{
    font-weight: 800;
    font-size: 2.8rem;
    text-align: right;
    line-height: 1;
}

/* courses section  */

.courses {
    padding: 12rem 0rem 8rem 0rem;
    background: linear-gradient(rgba(255, 255, 255, .7), rgba(255, 255, 255, .99)), url(/deeplizard-clone/static/media/courses-bg.3738acd2.jpg);
    background-position: 50% 40%;
}

.courses .course {
    text-align: center;
}

.courses h4
{
    font-weight: 600;
    font-size: 2rem;
}

.courses h1 {
    font-size: 55px;
    font-weight: 800;
}

.courses p
{
    font-size: 1.25rem;
    font-weight: 600;
}

/* welcome section  */

.welcome-section {
    padding-bottom: 7rem;
}

.welcome-section p
{
    font-size: 2.5rem;
    text-align: center;
    line-height: 1.2;
}

.welcome-section .special-text
{
    font-size: 3rem;
    font-weight: 800;
    color: #4b9097;
}

/* deeplearning section */

.deeplearning-section {
    padding: 3.5rem 0rem;
    background-color: #2c2f32f7;
}

.deeplearning-section h2 {
    font-weight: 800;
    font-size: 2.8rem;
    text-align: right;
    line-height: 1;
}

.deeplearning-section p {
    font-size: 1.1rem;
    font-weight: 300;
}

/* multiple courses section  */

.multiple-courses .item
{
    padding: 2rem 0rem 3rem 0.25rem;
}

.multiple-courses .item h2 {
    font-weight: 600;
}

.multiple-courses .lead
{
    font-weight: lighter;
    font-size: 1.1rem;
}

.multiple-courses .item:nth-child(even) {
    background-color: #fafafa;
}

@media (max-width: 600px) {
    .slider {
        padding: 4rem 1rem;
        margin-bottom: 0rem;
    }

    .slider h4 { font-size: 1rem !important; }

    .slider h1 { font-size: 3rem; }

    .after-slider h2 { font-size: 1.5rem; }

    .courses { padding: 10rem 0rem 8rem 0rem; }

    .welcome-section p { font-size: 1.75rem; }

    .welcome-section .special-text { font-size: 2.5rem; }

    .deeplearning-section h2 { font-size: 1.5rem; }

    .multiple-courses .item img { padding-bottom: 20px; }
}
.code-section img {
    width: 100%;
}

.resources p
{
    font-size: 1.08rem;
    font-weight: 100;
}

.collapse-item {
    margin-bottom: 20px;
    cursor: pointer;
    border-bottom: 1px solid #ebebeb;
    padding: 10px 0 10px 0;
}

@media only screen and (max-width: 600px) {
    .collapse-item h3 {
        font-size: 17px;
    }
}


